import React, { useState, useEffect, useRef, useCallback } from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import StartPage from '../StartPage/StartPage';
import TeamPage from '../TeamPage/TeamPage';
import HowWeWorkPage from '../HowWeWorkPage/HowWeWorkPage';
import PricePage from '../PricePage/PricePage';
import OfferPage from '../OfferPage/OfferPage';
import ContactPage from '../ContactPage/ContactPage';
import ReviewsPage from '../ReviewsPage/ReviewsPage';
import AdminLogin from '../AdminLogin/AdminLogin';


function App() {
  const [reviews, setReviews] = useState([]);
  const [isAdmin, setIsAdmin] = useState(false); //     
  const logoutTimerRef = useRef(null); //    
  const TIMEOUT_DURATION = 15 * 60 * 1000; // 15 

  //     ,   useCallback
  const startLogoutTimer = useCallback(() => {
    clearTimeout(logoutTimerRef.current); //   
    logoutTimerRef.current = setTimeout(() => {
      handleLogout(); //  logout  15 
    }, TIMEOUT_DURATION);
  }, [TIMEOUT_DURATION]);

  //    ,   useCallback
  const resetTimer = useCallback(() => {
    if (isAdmin) {
      startLogoutTimer(); //    
    }
  }, [isAdmin, startLogoutTimer]);

  //      
  useEffect(() => {
    const storedAdminStatus = localStorage.getItem('isAdmin');
    if (storedAdminStatus === 'true') {
      setIsAdmin(true);
      startLogoutTimer(); //    ,   
    }
  }, [startLogoutTimer]);

  useEffect(() => {
    //    
    window.addEventListener('mousemove', resetTimer);
    window.addEventListener('keydown', resetTimer);
    window.addEventListener('click', resetTimer);
    window.addEventListener('touchstart', resetTimer);

    return () => {
      //     
      window.removeEventListener('mousemove', resetTimer);
      window.removeEventListener('keydown', resetTimer);
      window.removeEventListener('click', resetTimer);
      window.removeEventListener('touchstart', resetTimer);
    };
  }, [resetTimer]);

  //       
  useEffect(() => {
    const fetchReviews = async () => {
      try {
        const response = await fetch('http://localhost:3001/reviews');
        const data = await response.json();
        setReviews(data); //     
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchReviews();
  }, []);

  const addReview = (review) => {
    setReviews([...reviews, review]);
  };

  const deleteReview = (index) => {
    const updatedReviews = reviews.filter((_, i) => i !== index);
    setReviews(updatedReviews);
  };

  const handleLogin = () => {
    setIsAdmin(true);
    localStorage.setItem('isAdmin', 'true'); //    localStorage
    startLogoutTimer(); //    
  };

  const handleLogout = () => {
    setIsAdmin(false);
    localStorage.removeItem('isAdmin'); //    localStorage
    clearTimeout(logoutTimerRef.current); //    
  };

  const specialists = [
    { id: 'natalia', name: 'Natalia Basko' },
    { id: 'sebastian', name: 'Sebastian Chmieliski' },
  ];

  return (
    <Router basename="/psycholog-warszawa">
      <div className="page-container">
        <Header isAdmin={isAdmin} onLogout={handleLogout} />  {/*      */}
        <div className="content-wrap">
          <Routes>
            <Route path="/" element={<Navigate to="/start" />} />
            <Route path="/start" element={<StartPage reviews={reviews} specialists={specialists} addReview={addReview}/>} />
            <Route path="/team" element={<TeamPage reviews={reviews} isAdmin={isAdmin} addReview={addReview}/>} />
            <Route path="/how-we-work" element={<HowWeWorkPage />} />
            <Route path="/price" element={<PricePage isAdmin={isAdmin} />} />
            <Route path="/offer" element={<OfferPage />} />
            <Route path="/contact" element={<ContactPage />} />
            <Route 
              path="/reviews" 
              element={<ReviewsPage 
                specialists={specialists} 
                addReview={addReview} 
                deleteReview={deleteReview} 
                reviews={reviews} 
                isAdmin={isAdmin} 
              />} 
            />
            <Route 
              path="/admin-login" 
              element={<AdminLogin onLogin={handleLogin} />} 
            />
          </Routes>
        </div>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
